  <template>
  <div class="main quality">
    <div class="main-search">
      <div class="search-item">
        <span class="item-left">开始日期</span>
        <span class="item-right">
         <el-date-picker
           v-model="search.beginCalldate"
           type="date"
           value-format="yyyy-MM-dd"
           placeholder="开始日期"
           @change="getSbeginTime">
         </el-date-picker>
        </span>
      </div>
      <div class="search-item">
        <span class="item-left">结束日期</span>
        <span class="item-right">
         <el-date-picker
           v-model="search.endCalldate"
           type="date"
           value-format="yyyy-MM-dd"
           placeholder="结束日期"
           @change="getSendTime">
         </el-date-picker>
        </span>
      </div>
      <div class="search-item">
        <span class="item-left">外呼团队</span>
        <span class="item-right">
           <el-select v-model="search.corp" multiple collapse-tags @change="changeStaff('corp')">
              <template v-for="item in teamData">
                  <el-option :label="item.memo" :value="item.id"  :key="item.id">{{item.memo}}</el-option>
              </template>
           </el-select>
        </span>
      </div>
      <div class="search-item">
        <span class="item-left">品牌</span>
        <span class="item-right">
          <el-select v-model="search.prjid_local" multiple collapse-tags @change="changeStaff('proj')">
              <template v-for="item in projData">
                  <el-option :label="item.memo" :value="item.id"  :key="item.id">{{item.memo}}</el-option>
              </template>
           </el-select>
        </span>
      </div>
      <button type="button" class="btn-search" @click="search_data">查询</button>
      <!-- <div class="search-item" style="clear: both;">
        <span class="item-left">排序字段</span>
        <el-radio-group v-model="radio1">
          <el-radio :label="1" style="width: 60px" @click.native.prevent="radio1_btn(1)">通话时长</el-radio>
          <el-radio :label="2" style="width: 60px" @click.native.prevent="radio1_btn(2)">置忙时长</el-radio>
          <el-radio :label="3" style="width: 90px" @click.native.prevent="radio1_btn(3)">通话时长占比</el-radio>
          <el-radio :label="4" style="width: 60px" @click.native.prevent="radio1_btn(4)">工作效率</el-radio>
          <el-radio :label="5" style="width: 80px" @click.native.prevent="radio1_btn(5)">成功数</el-radio>
        </el-radio-group>
        <span class="item-left">排序条件</span>
        <el-radio-group v-model="radio2">
          <el-radio :label="1" style="width: 30px" @click.native.prevent="radio2_btn(1)">升序</el-radio>
          <el-radio :label="2" style="width: 60px" @click.native.prevent="radio2_btn(2)">降序</el-radio>
        </el-radio-group>
      </div> -->
    </div>
   <div class="main-table">
     <div class="table-msg">
       <div class="msg-left">
         <p>查询符合上述条件的数据<span>{{list.totalCount}}</span>条</p>
       </div>
     </div>
       <el-table
        :data="list.data"
        style="width: 100%"
        border>
        <el-table-column
          prop="datetime"
          align="center"
          label="时间"
          :formatter="formatDate"
        width="100">
        </el-table-column>
        <el-table-column
          prop="cid"
          align="center"
          :formatter="formatCorp"
          label="外呼团队"
        width="80">
        </el-table-column>
        <el-table-column
          prop="prjid_local"
          align="center"
          :formatter="formatProj"
          label="品牌"
        width="80">
        </el-table-column>
        <el-table-column
          prop="uname"
          align="center"
          label="坐席工号"
        width="80">
        </el-table-column>
        <el-table-column
          prop="umemo"
          align="center"
          label="坐席姓名"
        width="80">
        </el-table-column>
        <el-table-column
          prop="firstsignon"
          align="center"
          label="首通电话开始时间"
        width="100">
        </el-table-column>
          <el-table-column
            prop="lastsignoff"
            align="center"
            label="末通电话结束时间"
          width="100">
          </el-table-column>
          <el-table-column
            prop="connect"
            align="center"
            label="通话量"
            width="80">
          </el-table-column>
          <el-table-column
            prop="leads"
             align="center"
             label="成功数"
            sortable
             width="100">
          </el-table-column>
          <el-table-column
            prop="worktime"
            align="center"
            label="工作时间"
          width="90">
          </el-table-column>
         <el-table-column
           prop="busytimes"
           align="center"
           label="置忙时间"
           sortable
           width="120">
         </el-table-column>
          <el-table-column
            prop="outwork"
            align="center"
            label="置闲时间"
          width="90">
          </el-table-column>
          <el-table-column
            prop="calltime"
            align="center"
            label="通话时长"
            sortable
            width="120">
          </el-table-column>
        <el-table-column
          prop="callRate"
          align="center"
          label="通话时长占比"
        width="120">
          <template slot-scope="scope">
            <p type="text" size="small">{{scope.row.callRate}}%</p>
          </template>
        </el-table-column>
        <el-table-column
          prop="efficiency"
          align="center"
          label="工作效率"
          sortable
        width="120">
          <template slot-scope="scope">
            <p type="text" size="small">{{scope.row.efficiency}}%</p>
          </template>
        </el-table-column>
        <el-table-column
          prop="payWorktime"
          align="center"
          label="结算工作时长"
        width="130">
        </el-table-column>
      </el-table>
     <page-table show_size="true" v-bind:cur="search.page" v-bind:total_page="list.totalPage" v-bind:total_count="list.totalCount" @change="change_data"></page-table>
    </div>
  </div>
</template>

<script>
import {format} from '@/config/util'
import pageTable from '@/components/pager'
import ajax from '@/config/ajax'
import Qs from 'qs'
export default {
  name: 'seat',
  components:{pageTable},
  data () {
    return {
      search: {
        corp: [], // 外呼团队
        prjid_local: [], // 品牌
        beginCalldate: new Date(), // 开始时间
        endCalldate: new Date(), // 结束时间
        orderbyCondition: 'calltime',  //默认排序
        orderbyFlag: false,  //是否倒叙
        size: 10, // 每页数量
        page: 1 // 页码
      },
      list: {
        data: [], // 数据集
        totalPage: 0, // 总页数
        totalCount: 0 // 总条数
      },
      teamData: [],  //外呼团队字典数组
      projData: [],   //品牌字典数组
      isCorpContainAll: true,     //外呼团队是否选则了全选
      isProjContainAll: true,     //品牌是否选则了全选
    }
  },
  created(){
    this.teamData = JSON.parse(localStorage.getItem("cropid"));
    this.projData = JSON.parse(localStorage.getItem("projid"));
    this.search.corp[0] = this.teamData[0].id;
    this.search.prjid_local[0] = this.projData[0].id;
    this.getInitialList()
  },
  methods: {
    // 时间选择器开始时间
    getSbeginTime (val) {
      this.beginCalldate = new Date(val);
    },
    // 时间选择器结束时间
    getSendTime (val) {
      this.endCalldate = new Date(val)
    },
    // 查询
    search_data () {
      if(this.search.corp.length == 0){
        this.$message({
          message: '请选择外呼团队',
          type: 'warning'
        });
        return
      }else if(this.search.prjid_local.length == 0){
        this.$message({
          message: '请选择品牌',
          type: 'warning'
        });
        return
      }
      this.search.page = 1
      this.getInitialList()
    },
    //格式化日期
    formatDate(row){
      var calldate = row.datetime.toString();
      return row.datetime = calldate.replace(/^(\d{4})(\d{2})(\d{2})$/, "$1-$2-$3");
    },
    //品牌及外呼团队多选
    changeStaff(type){
      if(type == 'corp'){
        if(this.isCorpContainAll){
          this.isCorpContainAll = false;   // 如果之前选中了全部，则变化后肯定不包含全部了
          this.search.corp.splice(0, 1);// 则删除第一个全部
        }else{
          this.isCorpContainAll = this.search.corp.some(value => value === 0)
          if(this.isCorpContainAll){
            this.search.corp = [0];
          }else{
            if(this.search.corp.length == this.teamData.length-1){
              this.search.corp = [0];
              this.isCorpContainAll = true;
            }
          }
        }
      }else if(type == 'proj'){
        if(this.isProjContainAll){
          this.isProjContainAll = false;   // 如果之前选中了全部，则变化后肯定不包含全部了
          this.search.prjid_local.splice(0, 1);// 则删除第一个全部
        }else{
          this.isProjContainAll = this.search.prjid_local.some(value => value === 0)
          if(this.isProjContainAll){
            this.search.prjid_local = [0];
          }else{
            if(this.search.prjid_local.length == this.projData.length-1){
              this.search.prjid_local = [0];
              this.isProjContainAll = true;
            }
          }
        }
      }
    },
    //请求获取数据
    getInitialList(){
      var params = {
        beginCalldate:format(this.search.beginCalldate, "yyyyMMdd"),
        endCalldate:format(this.search.endCalldate, "yyyyMMdd"),
        cid: this.search.corp.join(","),
        prjid_local: this.search.prjid_local.join(","),
        orderbyCondition:this.search.orderbyCondition,
        orderbyFlag:this.search.orderbyFlag,
        page:this.search.page,
        size:this.search.size
      }
      return ajax.request({
        method:'post',
        url:'userstat',
        headers: {
          'Content-Type': 'application/x-www-form-urlencoded; charset=UTF-8'
        },
        transformRequest: [function(params) {      //在请求之前对data传参进行格式转换
          params = Qs.stringify(params)
          return params
        }],
        data:params
      }).then((res)=>{
        this.list.data=res.data.list;
        this.list.totalCount=res.data.totalCount;
        this.list.totalPage=res.data.totalPage;
      }).catch((error)=>{
        console.log(error)
      })
    },
    //处理外乎团队的数据
    formatCorp(row){
      for(var i=0;i<this.teamData.length;i++){
        if(row.cid == this.teamData[i].id){
          return row.crop = this.teamData[i].memo;
        }
      }
    },
    //处理品牌的数据
    formatProj(row){
      var projdata = this.projData;
      projdata = projdata.slice(1);
      for(var i=0;i<projdata.length;i++){
        if(row.prjid_local == projdata[i].id){
          return row.porj = projdata[i].memo;
        }
      }
    },
    // 分页
    change_data (index, size) {
      this.search.page = index
      this.search.size = size
      this.getInitialList()
    },
  },
}
</script>

<style scoped>
  @import '../../assets/css/pages/main.css';
  .quality{
    padding-left: 15px;
  }
  .quality .el-input{
    width: 150px;
  }
  .el-select-dropdown__wrap ul li{
    padding-left: 20px !important;
    display: block;
  }
</style>



